<template xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div>
    <div class="bg"></div>
    <div class="container">
      <div class="line bouncein">
        <div class="xs6 xm4 xs3-move xm4-move">
          <div style="height:150px;"></div>
          <div class="media media-y margin-big-bottom">
          </div>
          <form id="loginForm" @submit.prevent="login" method="post">
            <div class="panel loginbox">
              <div class="text-center margin-big padding-big-top"><h1>后台管理中心</h1></div>
              <div class="panel-body" style="padding:30px; padding-bottom:10px; padding-top:10px;">
                <div class="form-group">
                  <div class="field field-icon-right">
                    <input type="text" class="input input-big" name="name" placeholder="登录账号" v-model="username"/>
                    <span class="icon icon-user margin-small"></span>
                  </div>
                </div>
                <div class="form-group">
                  <div class="field field-icon-right">
                    <input type="password" class="input input-big" name="password" placeholder="登录密码"
                           v-model="password"/>
                    <span class="icon icon-key margin-small"></span>
                  </div>
                </div>
                <div>
                  {{error}}
                </div>
              </div>
              <div style="padding:30px;"><input type="submit" class="button button-block bg-main text-big input-big"
                                                value="登录"></div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  // import jquery from '../../assets/js/jquery.js'
  // import pintuer from '../../assets/js/pintuer.js'
  import {requestLogin} from '../../api/api';

  export default {
    data() {
      return {
        username: '',
        password: '',
        error: ''
      }
    },
    methods: {
      login() {
        var username = this.username;
        var password = this.password;
        if (username == null || username == "") {
          alert("用户名不能为空!");
          return false;
        }

        if (password == null || password == "") {
          alert("密码不能为空!");
          return false;
        }
        var loginParam = {username: username, password: password};
        requestLogin(loginParam).then(data => {
          console.log(data);
          if (data.length == 0) {
            this.error = "用户名或密码错误";
            return false;
          } else {
            this.$router.push('/index')
          }
        });

        // this.$http.post("/api/login", {username: username, password: password}).then((res) => {
        //   console.log(res.data);
        //   if (res.data.length == 0) {
        //     this.error = "用户名或密码错误";
        //     return false;
        //   } else {
        //     this.$router.push('/index')
        //   }
        // });

      }
    }
  }

</script>
<style>
  @import "../../assets/css/pintuer.css";
  @import "../../assets/css/admin.css";
</style>



