<template>
  <div class="emp_contaner">
    <table>
      <tr>
        <th>头像</th>
        <th>用户名</th>
        <th>密码</th>
        <th colspan="2">操作</th>
      </tr>

      <tr v-for="user in userLists">

        <td><img src="../../assets/images/y.jpg" width="80px" height="80px"/></td>
        <td>{{user.username}}</td>
        <td>{{user.password}}</td>
        <td><router-link :to="{path: '/user_list',query: {uid : user.uid}}" @click.native="flushCom">删除</router-link></td>
        <td><router-link :to="{path: '/user_add',query: {uid : user.uid}}">修改</router-link></td>
      </tr>

    </table>
  </div>
</template>

<script type="es6">
  import {getUserList, deleteUserById} from '../../api/api';
  export default {
    data() {
      return {
        userLists: [
          {
            uid: -1,
            username: "",
            password: ""
          }
        ]
      }
    },
    created() {
      console.log("created 00>>");
      this.list();
      console.log("created111 >>");
    },
    methods: {
      list() {
        console.log("list >>");
        getUserList().then( data => {
          console.log(data)
          this.userLists = data;
        });

        // this.$http.get("/api/list").then((res) => {
        //   console.log(res.data)
        //   this.userLists = res.data;
        // })
      },
      flushCom(){
        var uid = this.$route.query.uid;
        console.log("uid : " + uid);
        if (typeof(uid) != "undefined") {
          //删除
          var param = {uid: uid};
          deleteUserById(param).then(data=>{
            this.list();
          });
          // this.$http.get("/api/delete", {params: {uid: uid}})
          //   .then((res) => {
          //     console.log(res);
          //     this.list();
          //   })
        }
      }
    }
  }
</script>
<style>
  @import "../../assets/css/tables.css";
</style>



