<template>
  <div>
    <form name="client_form" @submit.prevent="userAdd" method="post">
      <table>
        <input type="hidden" v-model="uid">
        <tr bgcolor="#f5f5f5">
          <td>
            <div><font>*</font> 姓名:</div>
          </td>
          <td>
            <input id="clientname" type="text" name="name" value="" v-model="username" size="30">
          </td>
        </tr>
        <tr bgcolor="#f0f0f0">
          <td>
            <div><font>*</font> 密码:</div>
          </td>
          <td><input id="password" type="password" name="password" value="" v-model="password" size="30"></td>
        </tr>

        <tr bgcolor="#f5f5f5">
          <td></td>
          <td colspan="2"><input type="submit" value="提交">
            <input type="reset" value="重置"></td>
        </tr>
      </table>
    </form>

  </div>
</template>

<script type="es6">
  import {findUserById,updateUser,addUser} from '../../api/api';
  export default {
    data() {
      return {
        uid: -1,
        username: '',
        password: ''
      }
    },
    created() {
      var uid = this.$route.query.uid;
      console.log("uid : " + uid);
      if (typeof(uid) != "undefined") {
        //修改
        const param = {id: uid};
        findUserById(param).then(data=>{
          this.username = data.username;
          this.password = data.password;
          this.uid = uid;
        });
        // this.$http.get("/api/find", {params: {id: uid}}).then((res) => {
        //   this.username = res.data.username;
        //   this.password = res.data.password;
        //   this.uid = uid;
        // })
      }
    },
    methods: {
      userAdd() {
        var name = this.username;
        var psw = this.password;
        if (name.trim() == "") {
          alert("请输入姓名");
        } else {
          if (this.uid != -1) {
            //修改
            var param = {username: name, psw: psw, id: this.uid};
            updateUser(param).then(data=>{
              console.log(data);
              this.$router.push('/user_list') //加载列表页
            });

            // this.$http.post("/api/update", {username: name, psw: psw, id: this.uid}).then((res) => {
            //   console.log(res);
            //   this.$router.push('/user_list') //加载列表页
            // })
          } else {
            const param = {username: name, psw: psw};
            addUser(param).then( data =>{
              console.log(data)
              this.$router.push('/user_list')
            })

            // this.$http.post("/api/add", {username: name, psw: psw}).then((res) => {
            //   console.log(res)
            //   this.$router.push('/user_list')
            // })
          }
        }
      }
    }
  }
</script>
<style scoped>
  table {
    background-color: #CCCCCC;
    border: 1px;
    width: 100%;
  }

  table tr {
    height: 20px;
  }

  table tr div {
    text-align: right;
  }

  td font {
    color: red;
  }

</style>



