import Vue from 'vue'
import Router from 'vue-router'
import Login from '../views/user/login'
import UserAdd from '../views/user/user_add'
import UserList from '../views/user/user_list'
import Index from '../views/index/index'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/user_add',
      component: UserAdd
    },
    {
      path: '/user_list',
      component: UserList
    }
  ]
})



