import axios from 'axios';

let base = 'http://127.0.0.1:8089';
// let base = '';

export const requestLogin = params => {
  return axios.post(`${base}/api/login`, params).then(res => res.data);
};

export const getUserList = params => {
  return axios.get(`${base}/api/list`, {params: params}).then( res => res.data);
};

export const deleteUserById = params => {
  return axios.get(`${base}/api/delete`, {params: params});
};

export const findUserById = params => {
  return axios.get(`${base}/api/find`,  {params: params});
};
export const updateUser = params => {
  return axios.post(`${base}/api/update`, params).then(res => res.data);
};

export const addUser = params => {
  return axios.post(`${base}/api/add`, params).then(res => res.data);
};



