<template>
  <div>
    <div class="header bg-main">
      <div class="logo margin-big-left fadein-top">
        <h1><img src="../../assets/images/y.jpg" class="radius-circle rotate-hover" height="50" alt=""/>后台管理中心</h1>
      </div>
      <div class="head-l"><a href="/" class="button button-little bg-blue"><span class="icon-wrench"></span>
        退出登录</a></div>
    </div>

    <div class="leftnav">
      <div class="leftnav-title"><strong><span class="icon-list"></span>菜单列表</strong></div>

      <h2><span class="icon-pencil-square-o"></span>用户管理</h2>
      <ul>
        <!--<li><router-link to='/user_add' target="right"><span class="icon-caret-right"/>添加用户</router-link></li>-->
        <li><a href="/user_add" target="right"><span class="icon-caret-right"></span>添加用户</a></li>
        <li><a href="/user_list" target="right"><span class="icon-caret-right"></span>用户列表</a></li>
      </ul>

      <h2><span class="icon-pencil-square-o"></span>商品管理</h2>
      <ul>
        <li><a target="right"><span class="icon-caret-right"></span>添加商品</a></li>
        <li><a target="right"><span class="icon-caret-right"></span>商品列表</a></li>
      </ul>

    </div>

    <div class="rightContent">
      <ul class="bread">
        <li><a href="{:U('Index/info')}" target="right" class="icon-home"> 首页</a></li>
        <li><a href="##" id="a_leader_txt">网站信息</a></li>
      </ul>
      <div class="admin">
        <iframe scrolling="auto" rameborder="0" src="html/info.html" name="right" width="100%" height="100%"></iframe>
      </div>
    </div>
  </div>
</template>

<script type="es6">

  export default {

    data() {
      return {
        isNowPage: true
      }
    },
    created() {
      $(function(){
        $(".leftnav h2").click(function(){
          $(this).next().slideToggle(200);
          $(this).toggleClass("on");
        })
        $(".leftnav ul li a").click(function(){
          $("#a_leader_txt").text($(this).text());
          $(".leftnav ul li a").removeClass("on");
          $(this).addClass("on");
        })
      });
    }
  }
</script>
<style>
  @import "../../assets/css/pintuer.css";
  @import "../../assets/css/admin.css";
</style>



